<template>
  <div class="mx-auto" style="max-width: 700px">
    <v-alert dark border="bottom" color="primary lighten-1">
      <p class="text-h6 text-center">Pilih sesuai bab dan materi</p>
      <!-- <p v-text="materi"></p> -->
      <v-select
        outlined
        dense
        label="Pilih Bab"
        :items="listBab"
        v-model="namaBab"
      ></v-select>
      <v-select
        outlined
        dense
        label="Pilih Materi"
        :items="listMateri"
        v-model="namaMateri"
        v-show="namaBab"
      ></v-select>
    </v-alert>
    <v-form v-show="namaMateri && namaBab">
      <v-alert
        border="top"
        class="elevation-3"
        colored-border
        color="primary lighten-2"
      >
        <v-text-field
          v-model="form.title"
          height="110%"
          @blur="defaultTitle"
          :style="{
            fontSize: $vuetify.breakpoint.mobile ? '1.7rem' : '2.2rem',
          }"
          color="purple"
        ></v-text-field>
        <v-text-field
          placeholder="Deskripsi Quiz"
          class="mt-n3"
          v-model="form.description"
          color="purple"
          dense
        >
        </v-text-field>
      </v-alert>

      <v-alert
        border="left"
        class="elevation-3"
        colored-border
        color="primary lighten-2"
        v-for="(forms, i) in form.questions"
        :key="i"
      >
        <v-text-field
          v-model="forms.question"
          filled
          dense
          label="Pertanyaan"
          color="purple"
        >
          <template v-slot:append-outer>
            <v-btn text color="purple" tabindex="-3" @click="tambahPertanyaan">
              <v-icon left>mdi-pencil-plus-outline</v-icon>
              <span class="hidden-xs-only"> Tambah </span>
            </v-btn>
          </template>
        </v-text-field>
        <v-text-field
          placeholder="Options"
          class="mt-n3"
          color="purple"
          v-for="(option, j) in forms.options"
          :key="j"
          v-model="option.option"
        >
          <template v-slot:prepend>
            <v-btn text icon tabindex="-3" @dblclick="asCorrect(i, j)">
              <div v-if="option.is_correct">
                <v-icon color="purple">mdi-checkbox-blank-circle</v-icon>
              </div>
              <v-icon v-if="!option.is_correct" color="purple"
                >mdi-checkbox-blank-circle-outline</v-icon
              >
            </v-btn>
          </template>
          <template v-slot:append-outer tabindex="-2">
            <v-btn
              tabindex="-3"
              text
              color="purple"
              @click="
                forms.options.length !== 1 ? forms.options.splice(j, 1) : ''
              "
            >
              <v-icon left>mdi-close</v-icon>
              <span class="hidden-xs-only">Hapus</span>
            </v-btn>
          </template>
        </v-text-field>
        <div class="d-flex">
          <v-btn
            text
            color="purple"
            @click="forms.options.push({ option: '', is_correct: 0 })"
          >
            <v-icon>mdi-plus</v-icon>
            Tambah Opsi
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn text color="purple" tabindex="-3" @click="hapusPertanyaan(i)">
            <v-icon left>mdi-delete-empty-outline</v-icon>
            <span class="hidden-xs-only"> hapus pertanyaan </span>
          </v-btn>
        </div>
      </v-alert>
      <v-btn color="purple" block dark @click="submit"> Submit </v-btn>
    </v-form>
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline"> Hapus Soal ? </v-card-title>

        <v-card-text>
          Anda tidak bisa meng-undo soal yang sudah dihapus!
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn color="green darken-1" text @click="dialog = false">
            Batal
          </v-btn>

          <v-btn color="error darken-1" text @click="confirmDelete(index)">
            Hapus
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { watch, onMounted, reactive, ref } from "@vue/composition-api";
export default {
  name: "QuizTambah",
  props: ["user"],
  setup(props, { root }) {
    const dialog = ref(false);
    let index = ref(null);
    const mapel = ref({});
    const listBab = ref([]);
    const listMateri = ref([]);
    const idBab = ref([]);
    const idMateri = ref([]);
    const namaBab = ref("");
    const namaMateri = ref("");
    watch(namaBab, (val) => {
      const bab = mapel.value.bab.filter((bab) => bab.babTitle === val)[0];
      idBab.value = bab._id;
      listMateri.value = bab.materi.map((val) => val.materiTitle);
    });
    watch(namaMateri, (val) => {
      const bab = mapel.value.bab.filter((bab) => bab._id === idBab.value)[0];
      const materi = bab.materi.filter(
        (materi) => materi.materiTitle === val
      )[0];
      idMateri.value = materi._id;
    });
    const form = reactive({
      title: "Judul Quiz",
      description: "",
      questions: [
        {
          question: "",
          options: [
            {
              option: "",
              is_correct: 0,
            },
          ],
        },
      ],
    });

    const submit = () => {
      const payload = {
        idMapel: mapel.value._id,
        idBab: idBab.value,
        idMateri: idMateri.value,
        type: "quiz",
        ...form,
      };
      console.log(payload);
      root.axios
        .post("user/pelajaran/mapel/submateri", payload)
        .then((res) => {
          root.bus.$emit("showSnackbar", {
            message: res.data.message,
            show: true,
          });
          root.$router.push("/");
        })
        .catch((err) => console.log(err));
    };
    const getData = () => {
      root.bus.$emit("loadingStart");

      root.axios
        .get("user/pelajaran/mapel/data/" + encodeURI(props.user.username))
        .then(({ data }) => {
          mapel.value = data.mapel;
          listBab.value = mapel.value.bab.map((val) => val.babTitle);
        })
        .catch((err) => {
          root.bus.$emit("loadingStop");
          err.response;
        })
        .then(() => root.bus.$emit("loadingStop"));
    };
    onMounted(() => {
      getData();
    });
    const defaultTitle = () => {
      let cek = !!form.title;
      cek ? "" : (form.title = "Judul Quiz");
    };
    const tambahPertanyaan = () => {
      return form.questions.push({
        question: "",
        options: [
          {
            option: "",
            is_correct: 0,
          },
        ],
      });
    };
    const hapusPertanyaan = (e) => {
      if (form.questions.length === 1) return;
      let isWritten = false;
      index = e;
      for (let option in form.questions[e].options) {
        if (form.questions[e].options[option].option.length !== 0) {
          isWritten = true;
        }
      }
      if (!!form.questions[e].question || isWritten) {
        dialog.value = true;
      } else {
        form.questions.splice(e, 1);
      }
    };
    const confirmDelete = (e) => {
      form.questions.splice(e, 1);
      dialog.value = false;
    };
    const asCorrect = (i, j) => {
      for (let option in form.questions[i].options) {
        form.questions[i].options[option].is_correct = 0;
      }
      form.questions[i].options[j].is_correct = 1;
    };
    return {
      form,
      defaultTitle,
      dialog,
      tambahPertanyaan,
      asCorrect,
      hapusPertanyaan,
      index,
      namaBab,
      namaMateri,
      confirmDelete,
      mapel,
      listBab,
      listMateri,
      submit,
    };
  },
};
</script>

